@mixin visually-hidden() {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.form {

  @media (min-width: 744px) {
    width: 50%;
    display: inline-block;
    vertical-align: top;

  }

  display: flex;
  flex-direction: column;
  // padding: 0 20px 16px 20px;

  &__item {
    padding: 0 20px 16px;

  }

  &__input {
    background-color: #f8f8fa;
    height: 48px;
    line-height: 48px;
    border-radius: 40px;
    width: 100%;
    padding-left: 16px;
    border: 1px solid transparent;
    color: #292929;

    &::placeholder {
      font-family: Inter, sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: 0;
      text-align: left;
      color: #7e7e83;
    }

    &:hover {
      border: 1px solid #d5d7de;
      box-sizing: border-box;

      &::placeholder {
        color: #292929;
      }
    }

    &:focus {
      border: 1px solid #292929;

    }
  }

  &__textarea {
    background-color: #f8f8fa;
    border-radius: 16px;
    border: 0;
    width: 100%;
    padding: 14px 16px;
    min-height: 148px;
    color: #292929;

    &::placeholder {
      font-family: Inter, sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: 0;
      text-align: left;
      color: #7e7e83;
    }

    &:hover {
      border: 1px solid #d5d7de;
      box-sizing: border-box;

      &::placeholder {
        color: #292929;
      }
    }
  }

  &__label {
    &:not(:focus):not(:active) {
      @include visually-hidden();
    }
  }

}
